/** @format */

#home {
	position: relative;
	background: url('../../assets/bgIMG.png');
	background-size: cover;
	background-repeat: repeat;
	background-position: center;
	.app__wrapper {
		padding: 0;
		.copyright {
			display: none;
		}
	}
}

.head-text-modified {
	font-size: 50pt;
	font-family: 'Luckiest Guy';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #fff;

	text-shadow: 0px -6px 0 #4d4d4d, 0px -6px 0 #4d4d4d,
		0px 6px 0 #4d4d4d, 0px 6px 0 #4d4d4d, -6px 0px 0 #4d4d4d,
		6px 0px 0 #4d4d4d, -6px 0px 0 #4d4d4d, 6px 0px 0 #4d4d4d,
		-6px -6px 0 #4d4d4d, 6px -6px 0 #4d4d4d,
		-6px 6px 0 #4d4d4d, 6px 6px 0 #4d4d4d,
		-6px 18px 0 #4d4d4d, 0px 18px 0 #4d4d4d,
		6px 18px 0 #4d4d4d, 0 19px 1px rgba(0, 0, 0, 0.1),
		0 0 6px rgba(0, 0, 0, 0.1), 0 6px 3px rgba(0, 0, 0, 0.3),
		0 12px 6px rgba(0, 0, 0, 0.2),
		0 18px 18px rgba(0, 0, 0, 0.25),
		0 24px 24px rgba(0, 0, 0, 0.2),
		0 36px 36px rgba(0, 0, 0, 0.15);
}
.app__header {
 flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 6rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
	flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }
}
.app__header-badge {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	flex-direction: column;

	.badge-cmp,
	.tag-cmp {
		padding: 1rem 2rem;
		border: var(--white-color);
		border-radius: 50px;
		flex-direction: row;
		width: auto;
		box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
		background: #ee9ca7; /* fallback for old browsers */
		background: -webkit-linear-gradient(
			to right,
			#ffdde1,
			#ee9ca7
		); /* Chrome 10-25, Safari 5.1-6 */
		background: linear-gradient(
			to right,
			#ffdde1,
			#ee9ca7
		); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	}

    .tag-cmp{
    flex-direction: column;
    margin-top:3rem;
    p{
    width:100%;
    text-transform:uppercase;
    text-align:right;


    }

    }
    span{
    font-size:2.5rem;

    @media screen and (min-width: 2000px){
    font-size:5rem;
    }

    }

    @media screen and (min-width: 1200px){
    justify-content: flex-start;
    align-items: flex-start;;
    }
}

.app__header-circles {
flex:0.75;
display: flex;
flex-direction:column;
justify-content: space-evenly;
align-items:flex-start;
height: 100%;
margin-left: 1rem;

    div {
    width:100px;
    height:100px;
    border-radius: 50%;

    background:var(--white-color);
    background: #ee9ca7;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #ffdde1, #ee9ca7);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffdde1, #ee9ca7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
        img {
        width:90%;
        height:90%;
        }
    }

    div:nth-child(1){
        margin:1.75rem;
        width:130px;
        height:130%;
    }
        div:nth-child(2){
        margin:1.25rem;
        width:120px;
        height:120px;
    }
        div:nth-child(3){
        margin:1.35rem;
        width:120px;
        height:120px;
    }
        div:nth-child(4){
        margin:1.25rem;
        width:100px;
        height:100px;
    }
        div:nth-child(5){
        margin:1.5rem;
        width:100px;
        height:100px;
    }
        div:nth-child(6){
        margin:1.15rem;
        width:70px;
        height:70px;
    }
        div:nth-child(7){
        margin:1.25rem;
        width:100px;
        height:100px;
    }

    @media screen and (min-width: 2000px) {
    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction:row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {

  flex: 1;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;

    object-fit: contain;
    z-index: 1;
  }
  .manloti{
  display: inline;


  }

  .overlay_circle {
  position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 90%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

